ay que olvidar y más bien toca recalcarlo, que para que la tarea de comunicar sea efectiva, no hay que limitarse solo a la entrega del documento, sino que hay que incluir la retroalimentación y la verificación de que los empleados hayan comprendido las políticas y les haya quedado claro qué repercusiones puede tener su incumplimiento en ellos. En la realidad, una empresa que logra comunicar bien sus políticas, reduce incidentes de seguridad por desconocimiento y aumenta la participación activa de su personal en la protección de la información.")])]),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("A continuación, se muestran diferentes métodos para comunicar políticas de seguridad y ejemplos que los relacionan en una pyme, los cuales se pueden utilizar según el escenario: ")])])])
